<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="">
          <template scope="scope">
            {{scope.row.create_time | formatDate}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button @click="addressVisible = true" size="mini" type="primary" icon="el-icon-edit"></el-button>
            <el-button @click="showProgressBox" size="mini" type="success" icon="el-icon-location"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 10, 15]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
        title=""
        @close="addressDialogClosed"
        :visible.sync="addressVisible">
      <el-form :model="addressForm" :rules="addressRules" ref="addressFormRef" label-width="85px">
        <el-form-item label="/" prop="address1">
          <el-cascader clearable :options="citydata" v-model="addressForm.address1"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="addressVisible = false"> </el-button>
        <el-button type="primary" @click="addressVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
        title=""
        :visible.sync="progressVisible">
      <el-timeline>
        <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>>

<script>
  import citydata from "@/pages/order/citydata";

  export default {
    name: 'Order',
    data(){
      return {
        // 
        orderList: [],
        // 
        queryInfo: {
          query: '',
          pagenum: 1,
          pagesize: 10
        },
        // 
        total: 0,
        // 
        addressVisible: false,
        // 
        addressForm: {
          address1: [],
          address2: ''
        },
        // 
        addressRules: {
          address1: [
            { trigger: 'change', required: true, message: '/' }
          ],
          address2: [
            { trigger: 'blur', required: true, message: '' }
          ]
        },
        // 
        citydata,
        // 
        progressVisible: false,
        // 
        progressInfo: []
      }
    },
    methods: {
      // 
      async getOrderList(){
        const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
        if(res.meta.status !== 200) return this.$message.error('')
        this.orderList = res.data.goods
        this.total = res.data.total
      },
      // pagesize 
      handleSizeChange(newSize){
        this.queryInfo.pagesize = newSize
        this.getOrderList()
      },
      // pagenum 
      handleCurrentChange(newPage){
        this.queryInfo.pagenum = newPage
        this.getOrderList()
      },
      // 
      addressDialogClosed(){
        this.$refs.addressFormRef.resetFields()
      },
      // 
      async showProgressBox(){
        /*const { data: res } = await this.$http.get('kuaidi/1106975712662')
        if(res.meta.status !== 200) return this.$message.error('')
        this.progressInfo = res.data*/
        // 
        this.progressInfo = [
          {
            "time": "2018-05-10 09:39:00",
            "ftime": "2018-05-10 09:39:00",
            "context": ",,",
            "location": ""
          },
          {
            "time": "2018-05-10 08:23:00",
            "ftime": "2018-05-10 08:23:00",
            "context": "[]  95338",
            "location": ""
          },
          {
            "time": "2018-05-10 07:32:00",
            "ftime": "2018-05-10 07:32:00",
            "context": " []",
            "location": ""
          },
          {
            "time": "2018-05-10 02:03:00",
            "ftime": "2018-05-10 02:03:00",
            "context": "[], []",
            "location": ""
          },
          {
            "time": "2018-05-09 23:05:00",
            "ftime": "2018-05-09 23:05:00",
            "context": " []",
            "location": ""
          },
          {
            "time": "2018-05-09 21:21:00",
            "ftime": "2018-05-09 21:21:00",
            "context": "[], []",
            "location": ""
          },
          {
            "time": "2018-05-09 13:07:00",
            "ftime": "2018-05-09 13:07:00",
            "context": " ",
            "location": ""
          },
          {
            "time": "2018-05-09 12:25:03",
            "ftime": "2018-05-09 12:25:03",
            "context": "",
            "location": ""
          },
          {
            "time": "2018-05-09 12:22:24",
            "ftime": "2018-05-09 12:22:24",
            "context": "HLA",
            "location": ""
          },
          {
            "time": "2018-05-08 21:36:04",
            "ftime": "2018-05-08 21:36:04",
            "context": "",
            "location": ""
          }
        ]
        this.progressVisible = true
      }
    },
    created() {
      this.getOrderList()
    },
    // bug
    beforeRouteEnter(to, from, next){
      next(vc => {
        sessionStorage.setItem('activePath', 'orders')
        vc.$store.dispatch('saveNavStatus', 'orders')
      })
    }
  }
</script>

<style lang="less" scoped>
  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 12px;
  }
  .el-table {
    margin-top: 15px;
    font-size: 12px;
  }
  .el-pagination {
    margin-top: 15px;
  }
  .el-cascader {
    width: 100%;
  }
</style>